@import url('https://fonts.googleapis.com/css2?family=Kalam:wght@300;400;700&family=VT323&display=swap');

.about-page{
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-left: 1%;
  .code-editor-wrap{
    width: 85%;
    height: 90%;
    background: linear-gradient(90deg, #000,  #000a);
    border-radius: 1rem;
    transition: 0.2s ease-out;
    .code-editor{
      width: 100%;
      height: 100%;
      padding: 1rem;
      overflow: scroll;
      transform: translateZ(8rem);
      &::-webkit-scrollbar{
        display: none;
      }

      .line{
        display: flex;
        align-items: center;
        height: fit-content;
        font-size: 1.7rem;
        margin: .6rem 0;
        .line-no{
          width: 3.5rem;
          display: flex;
          justify-content: center;
          color: #3b3553;
          font-family: 'VT323', monospace;
          font-size: 2.1rem;
        }
        .line-code {
          font-family: 'Kalam', cursive;
          font-weight: 700;
          margin-left: 2rem;
          height: fit-content;
          color: #fff;
          span {
            letter-spacing: .2rem;
          }
        }
      }
    }
  }
}